@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;700&display=swap);
.mainWrapper {
  margin: 50px 70px;
}

@media (max-width: 1000px) {
  .mainWrapper {
    margin: 50px 10px;
  }
}

.heading{
    text-align: center;
    margin-bottom: 50px;
    font-weight: 700;
    font-size: 35px;
    color: var(--primary-color);
}
.cards {
  display: flex;
  grid-gap: 40px;
  gap: 40px;
  flex-wrap: wrap;
  justify-content: center;
}

.cardWrapper {
    width: 300px;
    border-radius: 15px;
    text-align: center; 
    box-shadow: 2px 4px 18px -5px #000000;
    transition: 0.05s;
    background-color: #fff;
}

.cardWrapper:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05); 
    box-shadow: 2px 4px 28px -3px #000000;
}

.cardImage {
    width: 100%;
    height: 270px;
    object-fit: cover;
    border-radius: 15px;
}

.cardTitle {
    padding: 20px 0;
    font-weight: 300;
}

.cardBtn {
    padding: 15px 25px;
    margin-bottom: 20px;
    background-color: var(--primary-color);
    color: #fff;
    border: none;
    font-size: 16px;
    border-radius: 15px;
    cursor: pointer;
}
.detail {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.detailImg {
  width: 500px;
  border-radius: 20px;
  object-fit: cover;
}

.detailText {
  width: 650px;
  text-align: justify;
}

.detailTitle {
  text-align: center;
  margin-bottom: 20px;
}

.detailInstruction {
  margin-bottom: 30px;
}

.detailLink {
  text-decoration: none;
  color: #111;
  padding: 15px 25px;
  border: 1px solid var(--primary-color);
  cursor: pointer;
  border-radius: 20px;
  text-align: center;
}

.detailLink:hover {
  background-color: var(--primary-color);
  color: #fff;
}

.back{
    cursor: pointer;
    margin-left: 70px;
    margin-bottom: 20px;
}

.back:hover{
    color: var(--primary-color);
}

:root{
    --primary-color: #345B63;
}

*, *::after, *::before{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

body {
    font-family: 'Poppins', sans-serif;
    background-color: #f7adad;
}
